import React, { useState } from 'react';
import { connect } from 'react-redux';
import { loginRequest } from '../actions';
import { Link } from 'react-router-dom';
import googleIcon from '../assets/static/google-icon.png';
import twitterIcon from '../assets/static/twitter-icon.png';
import '../assets/styles/Login.scss'

const Login = props => {

  const [form, setValues] = useState({
    email: '',
  });

  const handleSubmit = event => {
    event.preventDefault();
    props.loginRequest(form);
    props.history.push('/');
  }

  const handleInput = event => {
    setValues({
      ...form,
      [event.target.name]: event.target.value
    })
  }

  return(
    <section className="login">
      <section className="login__container">
        <h2>Inicia sesin</h2>
        <form className="login__container--form" onSubmit={handleSubmit}>
          <input className="input" name="email" type="text" placeholder="Correo" onChange={handleInput} />
          <input className="input" name="password" type="password" placeholder="Contrasea" onChange={handleInput} />
          <button className="button">Iniciar sesin</button>
          <div className="login__container--remember-me">
            <label>
              <input type="checkbox" id="cbox1" value="first_checkbox" />Recurdame
            </label>
            <a href="/">Olvid mi contrasea</a>
          </div>
        </form>
        <section className="login__container--social-media">
          <div>
            <img src={googleIcon} />
            Inicia sesin con Google
          </div>
          <div>
            <img src={twitterIcon} />
            Inicia sesin con Twitter
            </div>
        </section>
        <p className="login__container--register">
          No tienes ninguna cuenta <Link to="/register">Regstrate</Link>
        </p>
      </section>
    </section>
  )
}

const mapDispatchToProps = {
  loginRequest,
}

export default connect(null, mapDispatchToProps)(Login);